import { ActivatedRoute, Router, ParamMap } from '@angular/router';
import { Component, OnDestroy, OnInit } from '@angular/core';
import { FormControl, FormGroup, Validators } from '@angular/forms';

import { Observable, of, Subscription } from 'rxjs';
import { switchMap } from 'rxjs/operators';

import { FaseCobranca } from '../models/fasecobranca.model';
import { FasecobrancaService } from '../services/fasecobranca.service';
import { TranslateService } from '@ngx-translate/core';
import { ToastrService } from 'ngx-toastr';
import { DecimalPipe } from '@angular/common';

import { NgbCheckBox } from '@ng-bootstrap/ng-bootstrap';
import { overlapValidator } from '../directives/overlap.directive';



@Component({
  selector: 'fasecobranca-edit',
  templateUrl: './fasecobranca-edit.component.html',
  styleUrls: ['../fasecobranca.component.scss']
})
export class FasecobrancaEditComponent implements OnInit, OnDestroy {

  subtitle: string;
  resourceId: string;
  openType: string;

  goBackLink: string;

  errors: string;

  filass$: Observable<any[]>;
  content: FaseCobranca;

  result: string;
  static LOCALE_KEY = 'currentLocale';

  faseCobrancaSubs: Subscription;

  contentForm = new FormGroup({
    "fila": new FormControl('', [
      Validators.required,
      Validators.maxLength(4)
    ]),
    "cdFaseCobranca": new FormControl('', [
      Validators.required,
      Validators.maxLength(4)
    ]),
    "dsFaseCobranca": new FormControl('', [
      Validators.required,
      Validators.maxLength(30)
    ]),
    "faixaInicial": new FormControl('', [
      Validators.required,
      Validators.min(0),
      Validators.maxLength(5)
    ]),
    "faixaFinal": new FormControl('', [
      Validators.required,
      Validators.min(0),
      Validators.maxLength(5)
    ]),
    "flagPermanencia": new FormControl('', [
      Validators.required,
      Validators.maxLength(1)
    ]),
    "diasPermanencia": new FormControl('', [
      Validators.required,
      Validators.maxLength(5)
    ])
  }, { validators: overlapValidator });

  constructor(private route: ActivatedRoute,
    private router: Router,
    private faseCobrancaService: FasecobrancaService,
    private toastr: ToastrService,
    public translate: TranslateService,
  ) {
    this.goBackLink = '/external-' + this.faseCobrancaService.getResourceName();
    this.filass$ = faseCobrancaService.filas$;
    translate.setDefaultLang('ptBR');
    const currentLocale = sessionStorage.getItem(FasecobrancaEditComponent.LOCALE_KEY);

    translate.setDefaultLang(currentLocale);
  }

  ngOnInit() {
    this.resourceId = this.route.snapshot.paramMap.get('code');
    this.openType = this.route.snapshot.paramMap.get('openType');
    this.subtitle = 'FASECOBRANCA.ITEMSUBTITLE.' + this.openType.toUpperCase();

    if (this.resourceId === null || this.resourceId == undefined) {
      this.faseCobrancaSubs = this.faseCobrancaService.startNewItem()
        .subscribe(faseCobranca => {
          this.content = faseCobranca;
          this.contentForm.patchValue(this.content);
        });
    } else
      this.faseCobrancaSubs = this.faseCobrancaService.selectItem(this.resourceId)
        .subscribe(faseCobranca => {
          this.content = faseCobranca;
          this.contentForm.patchValue(this.content);
        });
    this.errors = '';
  }

  ngOnDestroy() {
    console.log('Cleaning FaseCobranÃ§a...');
    if (this.faseCobrancaSubs != undefined) this.faseCobrancaSubs.unsubscribe();
  }

  save() {
    switch (this.openType) {
      case 'remove':
        this.faseCobrancaService.remove(this.content.fila, this.content.cdFaseCobranca, () => this.exit());
        /*this.faseCobrancaService.remove(this.content.fila,this.content.cdFaseCobranca)
        .subscribe(
          (fasecobranca:FaseCobranca) => {
            if(fasecobranca != null) this.router.navigate([this.goBackLink]);
          },
        );*/
        break;
      case 'change':
        if (this.content.faixaInicial > this.content.faixaFinal) {
          let msg = `Faixa inicial nÃ£o pode ser maior que a faixa final`;
          this.toastr.error(msg, "Erro:");
        }
        else if (this.faseCobrancaService.validChange(this.content.fila, this.content.cdFaseCobranca, this.content.faixaInicial, this.content.faixaFinal) === 0) {
          if (this.content.flagPermanencia == "N") this.content.diasPermanencia = 0;
          //this.faseCobrancaService.save(() => this.exit());
          this.faseCobrancaService.save(this.content, () => this.exit());
        }
        else {
          let msg = `JÃ¡ existe uma Fase de Cobranca cadastrada nessa faixa`;
          this.toastr.error(msg, "Erro:");
        }
        break;
      case 'create':
        if (this.content.faixaInicial > this.content.faixaFinal) {
          let msg = `Faixa inicial nÃ£o pode ser maior que a faixa final`;
          this.toastr.error(msg, "Erro:");
        }
        else if (this.faseCobrancaService.validItem(this.content.fila, this.content.cdFaseCobranca, this.content.faixaInicial, this.content.faixaFinal) === 0) {
          if (this.content.flagPermanencia == "N") this.content.diasPermanencia = 0;
          this.faseCobrancaService.include(this.content, () => this.exit());
        }
        else if (this.faseCobrancaService.validItem(this.content.fila, this.content.cdFaseCobranca, this.content.faixaInicial, this.content.faixaFinal) === 1) {
          let msg = `JÃ¡ existe uma Fase de Cobranca cadastrada com este cÃ³digo`;
          this.toastr.error(msg, "Erro:");
        } else {
          let msg = `JÃ¡ existe uma Fase de Cobranca cadastrada nessa faixa`;
          this.toastr.error(msg, "Erro:");
        }
        break;

      default:
        break;
    }
  }
  cancel() {
    this.faseCobrancaService.rollbackChanges();
    //this.content = this.originalCopy;
    //let contentId = this.content ? this.content.codigoCriterioRemuneracao : null;
    this.exit();
  }

  exit(): void {
    this.router.navigate([this.goBackLink]);
  }

  get diagnostic() { return JSON.stringify(this.content); }

}
