import { Component, OnDestroy, OnInit} from '@angular/core';
import { faTimes } from '@fortawesome/free-solid-svg-icons';
import { FasecobrancaService } from './services/fasecobranca.service';
import { Router, ActivatedRoute } from '@angular/router';
import { Observable, Subscription } from 'rxjs';
import { release } from '../environments/release';

@Component({
  selector: 'lib-root',
  templateUrl:'./fasecobranca.component.html', 
  styleUrls: ['./fasecobranca.component.scss']
})

export class FasecobrancaComponent implements OnInit, OnDestroy {

  goBackLink: string;
  openType:string = "";
  resourceId:string = "";
  faTimes = faTimes;

  showLoading: boolean = false;
  arqLoading$: Observable<boolean>;

  loadSubs:Subscription;
  release: string = release.version;

  constructor(private route: ActivatedRoute,
              private requestHandlerService: FasecobrancaService,
              private router: Router) {
      this.arqLoading$ = this.requestHandlerService.loading$;
      this.loadSubs = this.arqLoading$.subscribe((status)=>{
        this.showLoading = status;
      });
          
      this.goBackLink = '/external-'+ this.requestHandlerService.getResourceName();
   }

  ngOnInit() {
    this.route.params.subscribe(params => {
      console.log(params)
      if (params.hasOwnProperty("openType"))
        this.openType = params.get("openType");
      if (params.hasOwnProperty("code"))
        this.resourceId = params.get("code");
    });
  }

  ngOnDestroy() {
    console.log('Cleaning FaseCobranca...');
    if (this.loadSubs != undefined) this.loadSubs.unsubscribe();
  }
} 

