import { NgModule } from '@angular/core';
import { RouterModule } from '@angular/router';
import { FasecobrancaComponent} from './fasecobranca.component';
import { FasecobrancaListComponent } from './list/fasecobranca-list.component';
import { FasecobrancaEditComponent } from './editor/fasecobranca-edit.component';
import { ProjectConfigResolver } from './interceptors/project.config.resolver';

@NgModule({
  imports: [
      RouterModule.forChild([
        {path: '', component: FasecobrancaComponent,
          children: [
            {path: '', component: FasecobrancaListComponent},
            {path: ':openType',component: FasecobrancaEditComponent},
            {path: ':openType/:code',component: FasecobrancaEditComponent }
          ],resolve: {
            envConfiguration: ProjectConfigResolver
          }}
    ])
  ],
  exports: [RouterModule]
})
export class FasecobrancaRoutingModule { }