import { Component, OnInit, QueryList, ViewChildren } from '@angular/core';

import { DecimalPipe } from '@angular/common';
import { Router, ActivatedRoute } from '@angular/router';
import { TranslateService } from '@ngx-translate/core';
import { ToastrService } from 'ngx-toastr';

import { Observable } from 'rxjs';

import { FasecobrancaService } from '../services/fasecobranca.service';

import { FaseCobranca } from '../models/fasecobranca.model';

import { NgbdSortableHeader,SortEvent } from '../directives/sortable.directive';
import { faPlus, faPencilAlt, faTrashAlt, faSearch, faSort } from '@fortawesome/free-solid-svg-icons';


@Component({
  selector: 'fasecobranca-list',
  templateUrl: './fasecobranca-list.component.html',
  styleUrls: ['../fasecobranca.component.scss']
})
export class FasecobrancaListComponent implements OnInit {
  title = '';
  resourceName = '/';
  faPlus = faPlus;
  faPencilAlt = faPencilAlt;
  faTrashAlt = faTrashAlt;
  faSearch = faSearch;
  faSort = faSort;
  faseCobranca$ : Observable<FaseCobranca[]>;
  loading$: Observable<boolean>;
  total$: Observable<number>;
  static LOCALE_KEY = 'currentLocale';

  @ViewChildren(NgbdSortableHeader) headers: QueryList<NgbdSortableHeader>;
  constructor(private route: ActivatedRoute,
              private router: Router,
              private requestHandlerService: FasecobrancaService,
              private toastr: ToastrService,
              public translate:TranslateService
              ) 
    { 
      this.faseCobranca$ = requestHandlerService.items$;
      this.total$ = requestHandlerService.total$;
      this.loading$ = requestHandlerService.loading$;
      this.resourceName = '/external-'+ this.requestHandlerService.getResourceName();

      translate.setDefaultLang('ptBR');
      const currentLocale =  sessionStorage.getItem(FasecobrancaListComponent.LOCALE_KEY);
        
      translate.setDefaultLang(currentLocale);
    }

  get page() { return this.requestHandlerService.page; }
  get pageSize() { return this.requestHandlerService.pageSize; }
  get searchTerm() { return this.requestHandlerService.searchTerm; }
  set page(page: number) { this.requestHandlerService.page =  page;}
  set pageSize(pageSize: number) { this.requestHandlerService.pageSize =  pageSize; }
  set searchTerm(searchTerm: string) {  this.requestHandlerService.searchTerm = searchTerm; }

  onSort({ column, direction }: SortEvent) {
    // resetting other headers
    this.headers.forEach(header => {
      if (header.sortable !== column) {
        header.direction = '';
      }
    });

   // sorting items
   this.requestHandlerService.sortColumn = column;
   this.requestHandlerService.sortDirection = direction;
  }

  ngOnInit() {
    this.requestHandlerService.load();
  }

  create(): void {
    this.router.navigate([this.resourceName, 'create']);
  }

  change(indexId:string): void {
    this.router.navigate([this.resourceName, 'change', indexId]);
  }

  remove(indexId:string): void {
    this.router.navigate([this.resourceName, 'remove', indexId]);
  }

  clear(): void {
    this.searchTerm = "";
    this.headers.forEach(header => {
        header.direction = '';
    });
    this.requestHandlerService.load();
  }

}
