import { Injectable, Inject, PipeTransform } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { DecimalPipe } from '@angular/common';

import { SearchResult } from '../models/search-result.model';
import { State } from '../models/state.model';
//import { Observable, of } from 'rxjs';
//import { catchError, map, tap } from 'rxjs/operators';
import { BehaviorSubject, Observable, of, Subject, throwError } from 'rxjs';
import { catchError, debounceTime, delay, finalize, switchMap, tap } from 'rxjs/operators';

import { FaseCobranca } from '../models/fasecobranca.model';
import { SortDirection } from '../directives/sortable.directive';
import { HandleError, HttpErrorHandler } from './http-error-handler.service';
import { ToastrService } from 'ngx-toastr';
import { environment } from '../../environments/environment';


const httpOptions = {
  headers: new HttpHeaders({ 'Content-Type': 'application/json' })
};

function compare(v1, v2) {
  return v1 < v2 ? -1 : v1 > v2 ? 1 : 0;
}

function sort<FaseCobranca>(items: FaseCobranca[], column: string, direction: string): FaseCobranca[] {
  if (direction === '') {
    return items;
  } else {
    return [...items].sort((a, b) => {
      const res = compare(a[column], b[column]);
      return direction === 'asc' ? res : -res;
    });
  }
}

function matches(fasecobranca: any, fila: string, text: string, pipe: PipeTransform) {
  const term = text.toLowerCase();
  let item: FaseCobranca = fasecobranca as FaseCobranca;
  return item.fila.toLowerCase().includes(term);
}

function fullMatch(fasecobranca: any, cdFaseCobranca: string) {
  let item: FaseCobranca = fasecobranca as FaseCobranca;
  return (item.cdFaseCobranca === cdFaseCobranca);
}

function validItemC(fasecobranca: any, cdFaseCobranca: string) {
  let item: FaseCobranca = fasecobranca as FaseCobranca;
  if (item.cdFaseCobranca.includes(cdFaseCobranca)) {
    return item
  };
}

function validItemB(fasecobranca: any, fila: string, cdFaseCobranca: string, faixaInicial: number, faixaFinal: number) {
  let item: FaseCobranca = fasecobranca as FaseCobranca;
  if (item.fila.includes(fila)) {
    if (item.cdFaseCobranca !== cdFaseCobranca) {
      if ((faixaInicial >= item.faixaInicial && faixaInicial <= item.faixaFinal) || (faixaFinal >= item.faixaInicial && faixaFinal <= item.faixaFinal)) {
        return item
      }
    }
  };
}

@Injectable({
  providedIn: 'root'
})
export class FasecobrancaService {
  //private serviceUrl = '/conppwPadraoC9-war/ppwpadrao/v9/fasecobranca';
  private _serviceName = "FaseCobranca";
  private listaItems: FaseCobranca[] = [];
  private listaFilas: FaseCobranca[] = [];
  private _filas$ = new BehaviorSubject<FaseCobranca[]>([]);
  private _loading$ = new BehaviorSubject<boolean>(false);
  private _search$ = new Subject<void>();
  private _items$ = new BehaviorSubject<FaseCobranca[]>([]);
  private _total$ = new BehaviorSubject<number>(0);
  private handleError: HandleError;

  private originalCopy: FaseCobranca = null;

  private _state: State = {
    page: 1,
    pageSize: 10,
    codigo: '',
    searchTerm: '',
    sortColumn: '',
    sortDirection: ''
  }

  constructor(private http: HttpClient,
    private httpErrorHandler: HttpErrorHandler,
    private toastr: ToastrService,
    private pipe: DecimalPipe) {

    this.handleError = httpErrorHandler.createHandleError(this._serviceName);
    this.loadFilas();
    this._search$.pipe(
      tap(() => this._loading$.next(true)),
      debounceTime(200),
      switchMap(() => this._search()),
      delay(200),
      tap(() => this._loading$.next(false))
    ).subscribe(result => {
      this._items$.next(result.items);
      this._total$.next(result.total);
    });
    //this._search$.next();
  }

  get filas$() { return this._filas$.asObservable(); }
  get items$() { return this._items$.asObservable(); }
  get total$() { return this._total$.asObservable(); }
  get loading$() { return this._loading$.asObservable(); }
  get page() { return this._state.page; }
  get pageSize() { return this._state.pageSize; }
  get searchTerm() { return this._state.searchTerm; }

  set page(page: number) { this._set({ page }); }
  set pageSize(pageSize: number) { this._set({ pageSize }); }
  set sortColumn(sortColumn: string) { this._set({ sortColumn }); }
  set sortDirection(sortDirection: SortDirection) { this._set({ sortDirection }); }
  set codigo(codigo: string) { this._set({ codigo }); }
  set searchTerm(searchTerm: string) { this._set({ searchTerm }); }


  private _set(patch: Partial<State>) {
    Object.assign(this._state, patch);
    this._search$.next();
  }


  private _search(): Observable<SearchResult<FaseCobranca>> {
    if (this.listaItems.length <= 0) {
      let items = this.listaItems;
      let total = 0;
      return of({ items, total });
    }

    const { sortColumn, sortDirection, pageSize, page, searchTerm, codigo } = this._state;

    // 1. sort
    let items = sort(this.listaItems, sortColumn, sortDirection);

    // 2. filter
    if (searchTerm.length >= 2)
      items = items.filter(item => matches(item, codigo, searchTerm, this.pipe));

    // 3. paginate
    items = items.slice((page - 1) * pageSize, (page - 1) * pageSize + pageSize);


    let total = this.listaItems.length;
    //this.messageService.add("success", this._serviceName + ": Lista de items selecionados");

    return of({ items, total });

  }

  getResourceName(): string {
    return 'fasecobranca';
  }


  loadFilas(): void {
    //this.messageService.add("info", this._serviceName + " Lista de items selecionados");
    console.log(this._serviceName + " Lista de items selecionados");
    this.http.get<FaseCobranca[]>(`${environment.serviceUrls.serviceUrl}/fila/`)
      .pipe(tap(_ => console.log(`fetched items`)),
        catchError(this.handleError('filas', []))
      ).subscribe(filas => {
        this.listaFilas = filas;
        this._filas$.next(this.listaFilas);
      });
  }

  load(): void {
    this.getItems().pipe(catchError(this.handleError<FaseCobranca[]>('FaseCobranca', [])))
      .subscribe(items => {
        this.listaItems = items;
        this._items$.next(items);
        this._search$.next();
      });
  }

  getItems(): Observable<FaseCobranca[]> {
    this._loading$.next(true);
    console.log(this._serviceName + " Lista de items selecionados");
    return this.http.get<FaseCobranca[]>(`${environment.serviceUrls.serviceUrl}/getFaseCobranca`)
      .pipe(
        /*tap(
          data => this.log(`${this._serviceName}/load`, data),
          error => {
            console.log('Error')
            this.logError(`${this._serviceName}/load`, error)
          }
        ),*/
        finalize(() => this._loading$.next(false)),
        catchError(this.handleError<FaseCobranca[]>('Carregar itens', []))
      );
  }

  validItem(fila: string, cdFaseCobranca: string, faixaInicial: number, faixaFinal: number) {
    let items = this.listaItems.filter(item => validItemB(item, fila, cdFaseCobranca, faixaInicial, faixaFinal));
    let items2 = this.listaItems.filter(item => validItemC(item, cdFaseCobranca));
    if (items2.length !== 0) {
      return 1;
    }
    else if (items.length !== 0) {
      return 2;
    } else {
      return 0;
    }
  }

  validChange(fila: string, cdFaseCobranca: string, faixaInicial: number, faixaFinal: number) {
    let items = this.listaItems.filter(item => validItemB(item, fila, cdFaseCobranca, faixaInicial, faixaFinal));
    if (items.length !== 0) {
      return 2;
    } else {
      return 0;
    }
  }

  startNewItem(): Observable<FaseCobranca> {
    let selectedItem: FaseCobranca = new FaseCobranca();
    this.originalCopy = null;
    //this._selectedItem$.next(this.selectedItem);
    return of(selectedItem);
  }

  selectItem(cdFaseCobranca: string): Observable<FaseCobranca> {
    if (cdFaseCobranca === null || cdFaseCobranca === undefined) {
      console.log(`${this._serviceName}: cdFaseCobranca Ã© invÃ¡lido!`);
      let msg = `Fase de CobranÃ§a escolhido Ã© invÃ¡lido!`;
      this.toastr.error(msg, "CyberMessenger");
      return of(null);
    }
    let selectedItem: FaseCobranca = this.listaItems.find(item => fullMatch(item, cdFaseCobranca));
    this.originalCopy = JSON.parse(JSON.stringify(selectedItem));
    //this._selectedItem$.next(this.selectedItem);
    return of(selectedItem);
  }

  rollbackChanges() {
    if (this.originalCopy === null) return;
    let selectedIndex = this.listaItems.findIndex(item => fullMatch(item, this.originalCopy.cdFaseCobranca));
    this.listaItems[selectedIndex] = this.originalCopy;
    this._items$.next(this.listaItems);
  }

  include(item: FaseCobranca, done): void {
    //return this.http.put(`${environment.serviceUrls.serviceUrl}/${item[keyName]}`, item, httpOptions).pipe(
    this._loading$.next(true);
    let retVal: Observable<FaseCobranca> = this.http.put<FaseCobranca>(`${environment.serviceUrls.serviceUrl}`, item, httpOptions)

    retVal.pipe(
      finalize(() => this._loading$.next(false)),
      tap(
        data => {
          if (data === null)
            throwError(`Ocorreu um erro durante a incluisÃ£o do item cÃ³digo=${item['item.cdFaseCobranca']}.`);
          else
            console.log(`${this._serviceName}: Item cÃ³digo=${data['cdFaseCobranca']} incluido com sucesso!`);
        }
      ),
      catchError(this.handleError<FaseCobranca>('Incluir'))
      //`Erro durante a inclusÃ£o do item cÃ³digo=${item['rowId']}`
      //this.errors = 'Ocorreu um erro ao tentar guardar o critÃ©rio!';
    ).subscribe(response => {
      if (response) {
        let msg = `Fase de CobranÃ§a ${response['cdFaseCobranca']} salva com sucesso!`;
        this.toastr.success(msg, "CyberMessenger");
        //this.listaItems.push(item);
        this.listaItems.unshift(response);
        this._items$.next(this.listaItems);
        done();
      };
    }, error => {
      let msg = `Ocorreu uma falha ao incluir Fase CobranÃ§a  ${item.cdFaseCobranca}. Tente novemente!`;
      this.toastr.error(msg, "CyberMessenger");
      console.log('Erro ao remover registro: ' + error.error.message);
    });

  }

  save(item: FaseCobranca, done): void {
    this._loading$.next(true);
    let retVal: Observable<FaseCobranca[]> = this.http.post<FaseCobranca[]>(environment.serviceUrls.serviceUrl, [item], httpOptions);

    retVal.pipe(
      finalize(() => this._loading$.next(false)),
      tap(changedList => {
        if (changedList == null)
          throwError('Ocorreu um erro durante a atualizaÃ§Ã£o da Fase de cobranÃ§a.');
        else
          console.log(`${this._serviceName}: Fase de cobranÃ§a atualizada com sucesso!`);
      }

      ),
      catchError(this.handleError<FaseCobranca[]>('Salvar'))
      //`postItems cÃ³digo=${items.length}`
    ).subscribe(response => {
      if (response) {
        let msg = `Fase de CobranÃ§a alterada com sucesso!`;
        this.toastr.success(msg, "CyberMessenger");
        this._search$.next();
        done();
      };
    }, error => {
      let msg = `Ocorreu uma falha ao atualizar fase de cobranÃ§a  ${item.cdFaseCobranca}. Tente novemente!`;
      this.toastr.error(msg, "CyberMessenger");
      console.log('Erro ao remover registro: ' + error.error.message);
    });
  }
  //remove(fila: string, cdFaseCobranca: string): Observable<FaseCobranca> {
  remove(fila: string, cdFaseCobranca: string, done): void {
    this._loading$.next(true);
    let retVal: Observable<boolean> = this.http.delete<boolean>(`${environment.serviceUrls.serviceUrl}/${fila},${cdFaseCobranca}`, httpOptions);

    retVal.pipe(
      finalize(() => this._loading$.next(false)),
      tap(changed => {
        if (changed)
          console.log(`${this._serviceName}: Fase CobranÃ§a [${cdFaseCobranca}] removido com sucesso`);
      }),
      catchError(this.handleError(`Remover fase CobranÃ§a=${cdFaseCobranca}`, false))
    ).subscribe(response => {
      if (response) {
        let itemIndex = this.listaItems.findIndex((item) => item['cdFaseCobranca'] === cdFaseCobranca);
        if (itemIndex >= 0)
          this.listaItems.splice(itemIndex, 1);

        console.log('Registro removido com sucesso');
        let msg = `Fase CobranÃ§a removido com sucesso!`;
        this.toastr.success(msg, "CyberMessenger");
        this._items$.next(this.listaItems);
        done();
      } else {
        let msg = `Ocorreu uma falha ao remover Fase CobranÃ§a  ${cdFaseCobranca}. Tente novemente!`;
        this.toastr.error(msg, "CyberMessenger");
      }
      done();
    }, error => {
      let msg = `Ocorreu uma falha ao remover Fase CobranÃ§a  ${cdFaseCobranca}. Tente novemente!`;
      this.toastr.error(msg, "CyberMessenger");
      console.log('Erro ao remover registro: ' + error.error.message);
    });
  }

  filter(term: string, fila: string): Observable<FaseCobranca[]> {
    if (!term.trim()) {
      this.load();
    }
    //const url = `${this.httpUrl}/${environment.serviceUrls.serviceUrl}/?term=${term}`;
    const url = `${environment.serviceUrls.serviceUrl}/`;
    return this.http.get<FaseCobranca[]>(url).pipe(
      tap(_ => console.log(_)),
      catchError(this.handleError<FaseCobranca[]>('Filtrar', []))
    );
    //tap(_ =>console.log(`found items matching "${term}"`)),
  }

  /**
   * Handle Http operation that succeded.
   * @param operation - name of the operation that succeded
   * @param result - Result value to return as the observable
   */
  private log<FaseCobranca>(operation: string, result: FaseCobranca) {
    // TODO: better job of transforming error for user consumption
    console.log(`${operation} succeded`);

    // Let the app keep running by returning an empty result.
    //return of(result as T);
    return result;
  }

  private logError<FaseCobranca>(operation = 'operation', result?: FaseCobranca) {
    return (error: any): Observable<FaseCobranca> => {

      // TODO: send the error to remote logging infrastructure
      console.error(error); // log to console instead

      // TODO: better job of transforming error for user consumption
      console.log(`${operation} failed: ${error.message}`);

      // Let the app keep running by returning an empty result.
      return of(result as FaseCobranca);
    };
  }
}
