import { FormGroup, ValidationErrors, ValidatorFn } from "@angular/forms";

  export const overlapValidator: ValidatorFn = (control: FormGroup): ValidationErrors | null => {
    const minValue = control.get('faixaInicial').value;
    const maxValue = control.get('faixaFinal').value;
  
    if (maxValue<minValue)
        return {minOverlap: {minValue: minValue, maxValue: maxValue}};
    else if (minValue>maxValue) 
        return {maxOverlap: {minValue: minValue, maxValue: maxValue}};
    else return null;
  };